<template>

    <div class="tebel-info">
        <div class="goodsDetails" style="">
            <div class="headerListPage">
                <div>
                    <h2 class="pageLabel m-0">ClearanceAgentS DETAILS</h2>
                </div>
            </div>
            <div class="mt-5 ">
                <div class="GinfoDiv row align-items-center" style="background-color:#003F5E !important;">
                    <div class="col-3 text-center"><p style="color:white !important" class="m-0 TIG">ClearanceAgents Name</p></div>
                    <div class="col-3 text-center"><p style="color:white !important" class="m-0 TIG">ClearanceAgents Phone</p></div>
                    <div class="col-3 text-center"><p style="color:white !important" class="m-0 TIG">ClearanceAgents Email</p></div>
                    <div class="col-3"><p></p></div>
                </div>
                <div v-for="(ClearanceAgent,i) in ClearanceAgents" :key="i" class="GinfoDiv row align-items-center"  >
                    <div class="col-3 text-center"><p class="m-0 TIG">{{ClearanceAgent.name}}</p></div>
                    <div class="col-3 text-center"><p class="m-0 TIG">{{ClearanceAgent.phone}}</p></div>
                    <div class="col-3 text-center"><p class="m-0 TIG">{{ClearanceAgent.email}}</p></div>
                    <div class="col-3 d-flex gap-3 justify-content-center">
                        <img class='editIcon' style="width:50px;cursor: pointer;" @click="EditClearanceAgentBtn({'name':ClearanceAgent.name,'phone':ClearanceAgent.phone,'email':ClearanceAgent.email,'id':ClearanceAgent.id})" src="../assets/edit.png" alt="">
                        <img class='editIcon' style="width:50px;cursor: pointer;" @click="DeleteClearanceAgent(ClearanceAgent.id)" src="../assets/delete.png" alt="">
                    </div>
                </div>
            </div>
            <button @click="AddClearanceAgentBtn()" hidden id="addClearanceAgentsModalBtn" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#ClearanceAgentsModalBtn">
            </button>
            <form  @submit.prevent="this.requestType ==='ADD' ?AddClearanceAgent(this.ClearanceAgentInformation):EditClearanceAgent({'id':this.id,'info':this.ClearanceAgentInformation})">
            <div class="modal fade" id="ClearanceAgentsModalBtn" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" style="height:529px !important;max-width:846px !important;">
                    <div class="modal-content" style=" ">
                    <div class="modal-body d-flex flex-column" >
                        <div class="closeModel" data-bs-dismiss="modal" aria-label="Close">
                            <img src="../assets/close.png" class="w-50 h-50" alt="">
                        </div>
                        <h3 style="margin-top:16px">NEW ClearanceAgent</h3>
                        <p style="margin-top:24px">Insert Data To Generate New Clearance Agent</p>
                                <div  data-status="unOpen" class="textInputDiv textInputDivBorder mt-3"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-user-tie inputIcon"></i>
                                        <p @click.self="customInputText($event)" style="width:auto;white-space: nowrap;" class="m-0">ClearanceAgent Name</p>
                                    </div>
                                    <input  v-model="ClearanceAgentInformation.name"  @keyup="nv1 = ''"  class="customInput" type="text">
                                    <h5 id="" style="margin-left: 30px !important;white-space: nowrap;width:5%" class="m-0 inputTextValue">{{this.ClearanceAgentInformation.name}}</h5>
                                </div>
                                <p class="m-0" style="color:red">{{nv1}}</p>
                                <div  data-status="unOpen" class="textInputDiv textInputDivBorder mt-3"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-phone inputIcon"></i>
                                        <p @click.self="customInputText($event)" style="width:auto;white-space: nowrap;" class="m-0">Phone</p>
                                    </div>
                                    <input  v-model="ClearanceAgentInformation.phone"  @keyup="phoneV = ''"  class="customInput" type="text">
                                    <h5 id="" style="margin-left: 30px !important;white-space: nowrap;width:5%" class="m-0 inputTextValue">{{this.ClearanceAgentInformation.phone}}</h5>
                                </div>
                                <p class="m-0" style="color:red">{{phoneV}}</p>
                                <div  data-status="unOpen" class="textInputDiv textInputDivBorder  mt-3"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-at inputIcon"></i>
                                        <p @click.self="customInputText($event)" style="width:auto;white-space: nowrap;" class="m-0">Email</p>
                                    </div>
                                    <input  v-model="ClearanceAgentInformation.email"  @keyup="emailV = ''"  class="customInput" type="email">
                                    <h5 id="" style="margin-left: 30px !important;white-space: nowrap;width:5%" class="m-0 inputTextValue ">{{this.ClearanceAgentInformation.email}}</h5>
                                </div>
                                <p class="m-0" style="color:red">{{emailV}}</p>
                                </div>
                                <div style="background: #FFFFFF;box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);border-radius: 15px 15px 0px 0px;height:104px;display: flex;justify-content: center;align-items: center;">
                                    <button type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important" >{{requestType}} Clearance Agent</button>
                                </div>   
                        </div>
                    </div>
            </div>
            </form>        
            <button hidden id="ClearanceAgentResponsModelButtom" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#ClearanceAgentResponsModel">
            </button>
            <div class="modal fade" id="ClearanceAgentResponsModel" tabindex="-1" aria-labelledby="ClearanceAgentResponsModelLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-body d-flex flex-column">
                            <img src="../assets/bigCheck.png" alt="">
                            <h3 id="ClearanceAgentResponsText" style="margin-top:16px"></h3>
                            <button class="btn prymaryBtn w-75 " style="color:white;height:50px;margin-top:25px" data-bs-dismiss="modal" aria-label="Close" >Done</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <button hidden id="erorrcabtn" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#erorr_ca">
              </button>
              
              <div class="modal fade" id="erorr_ca" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                  <div class="modal-content">
                    <div class="modal-body d-flex flex-column">
                        <i style="color:red;font-size:xx-large" class="fa-solid fa-circle-exclamation"></i>
                        <h3 style="margin-top:16px">ERROR</h3>
                            <h5>can not delete this clearance agent</h5>
                        <button class="btn prymaryBtn w-75" style="color:white;height:50px;margin-top:25px" data-bs-dismiss="modal" aria-label="Close" >Done</button>
                    </div>
                  </div>
                </div>
              </div>
    </div>
</template>

<script>
import Header from '@/components/Custom_Input.vue'
/* eslint-disable */  import functions from  "../assets/js/js.js";
import { mapActions,mapGetters  } from 'vuex';
export default {
name: 'ClearanceAgentsDetails',
components: {
    Header,
}, 
    data(){
        return{
            ClearanceAgentInformation:{
                name:'',
                phone:'',
                email:'',
            },
            requestType:'',
            id:'',
            nv1:'',
            phoneV:'',
            emailV:'',
        }
    },
    methods:{
    async DeleteClearanceAgent(id){
        this.$store.dispatch('DeleteClearanceAgent',id)
    },
    customInput(el){
                functions.customInputa(el)
            },
        customInputText(el){
        el.target.parentElement.parentElement.click()
        },
    Sucond_Btn(){
        const Button = document.getElementById('Sucond_Btn')
        Button.style.display='none';
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='none';
    },
    First_Btn(){
        const First_Btn = document.getElementById('First_Btn')
        First_Btn.style.display='flex';
        First_Btn.querySelector('p').innerText='Add New'
        // First_Btn.querySelector('p').style.fontSize='x-small'
        First_Btn.addEventListener('click',function() {document.getElementById('addClearanceAgentsModalBtn').click();})
    },
    // ...mapActions(['AddAction']),
    AddClearanceAgent(info){
        if(this.validation()){
            this.$store.dispatch('AddClearanceAgent', info)
        }
    },
    AddClearanceAgentBtn(){
        this.requestType = 'ADD'
        this.ClearanceAgentInformation.name =''
        this.ClearanceAgentInformation.phone = ''
        this.ClearanceAgentInformation.email =''
    },
    EditClearanceAgentBtn(info){
        console.log(info)
        document.getElementById('addClearanceAgentsModalBtn').click()
        this.ClearanceAgentInformation.name = info.name
        this.ClearanceAgentInformation.phone =  info.phone
        this.ClearanceAgentInformation.email = info.email
        this.id = info.id
        this.requestType = 'EDIT'
    },
    EditClearanceAgent(info){
        if(this.validation()){
            this.$store.dispatch('EditClearanceAgentAction', info)
        }
    },
    validation(){
        var v = true
        if(this.ClearanceAgentInformation.name.length <= 0){
            this.nv1 = 'This Input Is Required '
            v = false
        }
        if(this.ClearanceAgentInformation.phone.length <= 0){
            this.phoneV = 'This Input Is Required'
            v = false
        }
        if(this.ClearanceAgentInformation.email.length <= 0){
            this.emailV = 'This Input Is Required'
            v = false
        }
        return v
    },
    },
    computed:{
        ...mapGetters(['ClearanceAgents'])
    },
    created(){
        document.getElementById('navaya').style.display = 'block'
    document.getElementById('h1h').style.display = 'flex'
    document.getElementById('h2h').style.display = 'none'
        this.Sucond_Btn()
        this.First_Btn()
    },
    mounted() {
    if ( !window.localStorage.token){
    this.$router.push({name:'Login'})
    }
    const ClearanceAgents = this.$store.dispatch('GetClearanceAgent')
    console.log(ClearanceAgents)
  }
}
</script>

