<template>
    <div class="tebel-info">
        <div class="listDiv" style="">
            <div class="headerListPage">
                <div>
                    <h2 class="pageLabel m-0">Latest Orders</h2>
                </div>
                <div class="d-flex headerListPageBtns">
                    <button class="prymaryBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">All</button>
                    <button class="secondBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">Shipping</button>
                    <button class="secondBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">In the Sea</button>
                    <button class="secondBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">Arrived</button>
                    <button class="secondBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">With Clearance Agent</button>
                    <button class="secondBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">Needs attention</button>
                    <button class="secondBtn containerFilter" @click="containerFilterActive($event),filter($event)" style="margin-right: 10px;">Finished</button>
                </div>
            </div>
            <div class="contanetList" >
                <div class="orderDiv mt-4" v-for="(order,i) in Orders"  v-bind:key="i">
                    <div class="orderDivS1 ">
                        <div class="d-flex align-items-center h-100">
                            <p class=" orderText x">Ref: 0902934234</p>
                            <p class="m-0 orderText">Documents Status: {{order.doc_status}}</p>
                        </div>
                        <div class="d-flex align-items-center">
                            <div class="me-2" style="width:18px;height:18px;background: #6AAA69;border-radius:50%"></div>
                            <p class="x2 orderText" style="color:#6AAA69">{{order.current_status}}</p>
                            <router-link v-if="order.clearing_status !== 'Ready To Sale'"  :to="{ name: 'Edit', params: { GoodName: order.id }}"><img style="width:50px;cursor:pointer" class="editIcon" src="../assets/edit.png" alt=""></router-link>
                        </div>
                    </div>
                    <div class="orderDivS2 ">
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-tags "></i>
                                <p class="m-0">Brand</p>
                            </div>
                            <!-- <h6 v-for="b in order.details" :key="b" class="text-center m-0">{{b.brand_name}}</h6> -->

                            <h6 v-if="order.details.length > 1"  class="text-center m-0">{{order.details[0].brand_name}} ...</h6>
                            <h6 v-else class="text-center m-0">{{order.details[0].brand_name}}</h6>
                        </div>
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-box "></i>
                                <p class="m-0">Goods Name</p>
                            </div>
                            <h6 v-if="order.details.length > 1"  class="text-center m-0">{{order.details[0].goods_name}} ...</h6>
                            <h6 v-else class="text-center m-0">{{order.details[0].goods_name}}</h6>
                        </div>
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-box"></i>
                                <p class="m-0">Container</p>
                            </div>
                            <h6 v-if="order.details.length > 1"  class="text-center m-0">{{order.details[0].container_data}} ...</h6>
                            <h6 v-else class="text-center m-0">{{order.details[0].container_data}}</h6>
                        </div>
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-plus-minus"></i>
                                <p class="m-0">Amount</p>
                            </div>
                            <h6 class="text-center m-0">{{order.payment}}</h6>
                        </div>
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-user-tie"></i>
                                <p class="m-0">Sublier</p>
                            </div>
                            <h6 class="text-center m-0">{{order.supplier}}</h6>
                        </div>
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-building-columns"></i>
                                <p class="m-0">Bank Name</p>
                            </div>
                            <h6 class="text-center m-0">{{order.bank_name}}</h6>
                        </div>
                        <div class="info ">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-circle-dollar-to-slot"></i>
                                <p class="m-0">B/l No.</p>
                            </div>
                            <h6 class="text-center m-0">{{order.bi_number}}</h6>
                        </div>
                        <div class="info">
                            <div class="d-flex align-items-center">
                                <i class="fa-solid fa-clipboard"></i>
                                <p class="m-0">Pay Amount</p>
                            </div>
                            <h6 class="text-center m-0" style="color:#6AAA69">{{order.payment}} $ </h6>
                        </div>
                        <div style="margin-left:auto">
                            <i class="fa-solid fa-angle-right"></i>
                        </div>
                    </div>
                </div>
            
            </div>
            <div class="b" style="height:200px;display:none;">
            </div>
            <button id="addPage" hidden @click="this.$router.push({ name: 'AddPage' })"></button>
        </div>
    </div>

</template>

<script>
// import _axios from '../axios'
import { mapGetters  } from 'vuex';
export default {
name: 'orderList',
components: {

}, 
    data(){
        return{
            containers:[]
        }
    },
    methods:{
        
        containerFilterActive(el){
          var containerFilterActive= document.getElementsByClassName('containerFilter')
          for(var c=0; c<containerFilterActive.length; c++){
            containerFilterActive[c].setAttribute('class', 'secondBtn containerFilter');
          }
          el.currentTarget.className = "prymaryBtn containerFilter"; 
      },
    Sucond_Btn(){
        const Button = document.getElementById('Sucond_Btn')
        Button.style.display='none';
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='none';
    },
    // addPush(){
    //     this.$router.push('/AddPage');
    // },
    First_Btn(){
        const First_Btn = document.getElementById('First_Btn')
        First_Btn.style.display='flex';
        First_Btn.querySelector('p').innerText='ADD NEW ORDER'
        First_Btn.addEventListener('click',function(){document.getElementById('addPage').click()})
    },
    getContainer(){
        this.containers=this.$store.getters.Orders
    },
    filter(el){
        if (el.currentTarget.innerText !== 'All'){
            this.$store.dispatch('Filter',{qurey:el.currentTarget.innerText})
        }else{this.$store.dispatch('GetOrders')}

    },
    },
    computed:{
        ...mapGetters(['Orders']),
        
    },
    created(){
        document.getElementById('navaya').style.display = 'block'
        document.getElementById('h1h').style.display = 'flex'
        document.getElementById('h2h').style.display = 'none'

        this.$store.dispatch('GetOrders')
        this.Sucond_Btn()
        this.First_Btn()
        // functions.addOrderNav()
        setTimeout(function(self){
            self
        this.getContainer()
        }.bind(this), 2000); //
    },
    mounted(){
        if ( !window.localStorage.token){
      this.$router.push({name:'Login'})
    }
    }
}
</script>

<style>
.headerListPageBtns{
    flex-wrap: wrap;
}
.listDiv{
    width: 100%; 
}
.pageLabel{
    font-family: 'Poppins';
font-style: normal;
font-weight: 700;
font-size: 30px;
line-height: 45px;
text-transform: uppercase;
color: #000000;
}
.headerListPage{
    display: flex;
   
    justify-content: space-between;
    align-items: center;
}
i{
    margin-right: 0 !important;
}
@media only screen and (max-width: 992px){
    .listDiv{
        border-left: none;
    }
    .b{
        display: block !important;
    }
}
@media only screen and (max-width: 1642px){
    .headerListPage{
        flex-direction: column;
    }
    .headerListPageBtns button{
        margin-top: 15px !important;
    }
}
@media only screen and (max-width: 768px){
    .listDiv{
        width:750px !important;
        padding: 1px !important;
        border: none !important;
    }
}
</style>



