
<template>
        
        <div class="header">
          <div class="logoDiv">
            <img class="logo" alt="logo" src="../assets/logo.png">  
            <p class="m-0">Dashboard</p>
          </div>
          <div  v-if="$route.fullPath !== '/Login'"  class="headerS2">
            <div class="buttonsDiv d-flex " >
              <div class="addBtnOrder" id="First_Btn" style="background: linear-gradient(268.77deg, #6AAA69 3.02%, #003F5E 138.89%);cursor: pointer;">
                <img src="../assets/plusW.png" alt="">
                <p class="m-0"> Add new Order</p>
              </div>
              <div class="addBtnOrder" id="Sucond_Btn" style="display: none;background-color:#003F5E !important;cursor: pointer;">
                <img src="../assets/plusW.png" alt="">
                <p  class="m-0"> Add new Order</p>
              </div> 
              <div class="historyBtn" id="historyBtn" style="width:74px;cursor:pointer;" @click="this.$router.push({name:'History'})" >
                <img src="../assets/HISTORY.png" alt="" class="w-100 w-100" style="">
              </div> 
            </div>
            <div class="userSearchDiv">
              <div class="user" id="user">
                <img src="../assets/userIcon.png" alt="">
                <p class="m-0 text-center">{{user}}</p>
              </div>
              <div v-if="$route.fullPath === '/goods-project/'"  class="headerSerch" id="headerSerch">
                <img src="../assets/serch.png" alt="">
                <form v-on:submit.prevent="serch()">
                  <input @keyup="serch()" id="serchInput" type="text" v-model="serchInput" placeholder="Search for...">
                </form>
              </div>
            </div>
          </div>
        </div>
  </template>

  <script>
  import { mapGetters  } from 'vuex';

   export default {
    name: 'HeaderSction',
    data(){
      return{
        serchInput:'',
      }
    },
    components: {
    },
    methods:{
      serch(){
        if (this.serchInput){
          this.$store.dispatch('SerchOrders',{qurey:this.serchInput})
          this.$router.push({'name':'orderList'})
        }else{
          this.$store.dispatch('GetOrders')
        }
      }
    },
    created(){

    },
    computed:{
      ...mapGetters(['user'])
    }
  }

</script>

<style>
