import _axios from '../axios'

export default({
    state:{
       InfoList:[]
    },
    getters: {
        ClearanceAgents: state  => state.InfoList
    },
    mutations:{
        GetClearanceAgent(state, data) {
            state.InfoList = data;
          },
    },
    actions:{
        AddClearanceAgent(contex,payload){
            _axios.post('/api/clearance_agents',payload, { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
                response
                contex.dispatch('GetClearanceAgent')
                document.getElementById('ClearanceAgentResponsText').innerText = 'Add Successfully'
                document.getElementById('ClearanceAgentResponsModelButtom').click()
            })
            .catch(error => {
                console.log('Error')
                console.error(error.response.data);
            });
        },
        DeleteClearanceAgent(contex,id){
            _axios.delete(`/api/clearance_agents/${id}/`,{
            headers: {
                'Authorization': `token ${window.localStorage.token}`
            },
          })
       .then(response => {
        response
        console.log('Response')
        contex.dispatch('GetClearanceAgent')
        document.getElementById('ClearanceAgentResponsText').innerText = 'Delete Successfully'
        document.getElementById('ClearanceAgentResponsModelButtom').click()
       })
        .catch(error => {
            document.getElementById('erorrcabtn').click()
           console.error(error.response.data);
        });
        },
        EditClearanceAgentAction(contex,payload){
            _axios.put(`/api/clearance_agents/${payload.id}/`,payload.info,{ headers: {
                'Authorization': `token ${window.localStorage.token}`}})
            .then(response => {
                response
                contex.dispatch('GetClearanceAgent')
                document.getElementById('ClearanceAgentResponsText').innerText = 'Edit Successfully'
                document.getElementById('ClearanceAgentResponsModelButtom').click()
            })
            .catch(error => {
                console.log('Error')
                console.error(error.response.data);
            });
        },
        GetClearanceAgent({ commit }){ 
            _axios.get('/api/clearance_agents', { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
              commit('GetClearanceAgent', response.data);
          })
          .catch(error => {
              console.error(error);
          });}
    },
  })