    submit(){
        console.log('==========')
        if(this.validetion()){
            if(this.$route.params.GoodName){
                const Edit = this.$store.dispatch('EditOrder',{id:this.$route.params.GoodName,data:this.orderInfo})
            }else{
                const store = this.$store.dispatch('AddOrder',this.orderInfo)
            }  
