            _axios.get('/api/suppliers/search/',{ 
                headers: {
                'Authorization': `token ${window.localStorage.token}`},
                 params: {
                    'name': value,
                }})
                .then(response => {
                this.suppliersList = [response.data]
            })
            .catch(error => {
                console.error(error.response);
            });
        }
    },
 
    },
    watch:{
        doc_status:function(value){
            if(value === 'Arrived to bank'){
                let today = new Date().toISOString().slice(0, 10)
                console.log(today)
                this.orderInfo.document_receive_at = today
            }
        }
    },
    computed:{
        ...mapGetters(['Goods','s','errors','ClearanceAgents']),
    },
    created(){
        this.dNavBtn()
        this.First_Btn()
        document.getElementById('navaya').style.display = 'block'
        document.getElementById('h1h').style.display = 'none'
        document.getElementById('h2h').style.display = 'flex'
        // this.CustoStyle()
        // this.$store.dispatch('GetGood')
        this.$store.dispatch('Getstore')
        this.$store.dispatch('GetOrders')
        this.$store.dispatch('GetClearanceAgent')
        // this.suppliers()
        // this.brands()
