    mounted(){
        if ( !window.localStorage.token){
            this.$router.push({name:'Login'})
        }
        console.log(!this.$route.fullPath === '/AddPage/')
        if(this.$route.fullPath.length > 9){
            _axios.get(`/api/containers/${this.$route.params.GoodName}`, { headers: {
                    'Authorization': `token ${window.localStorage.token}`}
                  })
                .then(response => {

                  this.orderInfo = response.data
                  console.log(response.data)
                  this.goods_count = response.data.details.length
                  for(var g = 0; g < response.data.details.length ; g++){
                      this.goods_validation.push({good:''})
                      this.brands_validation.push({brand:''})
                      var good_name = response.data.details[g].goods_name
                      this.SerchGoodName(good_name)
                //       setTimeout(() => {
                //   }, 1000);
                  }
              })
              .catch(error => {
                  console.error(error);
              });
        }else{
            
        }
          
