                        <h3 style="margin-top:16px">NEW REPORT</h3>
                        <p style="margin-top:24px">Select Report type to generate new report</p>
                            <div class="d-flex headerListPageBtns" style="margin-bottom:10px;width: 75%;    justify-content: center;">
                                <a @click="ActiveDateBtn($event),change('month')" class="secondBtn secondBtnActive  TypeDateBtn  btn-text" style="margin-right: 10px;">Monthly</a>
                                <a @click="ActiveDateBtn($event),change('daily')" class="secondBtn TypeDateBtn btn-text" style="margin-right: 10px;">Daily</a>
                                <a @click="ActiveDateBtn($event),change('year')" class="secondBtn TypeDateBtn btn-text" style="margin-right: 10px;">Yearly</a>
                                <a @click="ActiveDateBtn($event),change('between')" class="secondBtn TypeDateBtn btn-text" style="margin-right: 10px;">between</a>
                            </div>

                            <div  v-if="this.dateTypr == 'month'"  style=" border: 1px solid #6AAA69 !important;border-radius: 15px;" class=" mt-4 ">
                                <div  data-status="unOpen" class="textInputDiv"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-calendar inputIcon"></i>
                                        <p @click.self="customInputText($event)" class="m-0">MONTHLY</p>
                                    </div>
                                    <input   v-model="monthly" class="customInput" type="number" min="1" max="12"   >
                                    <h5  id="" style="margin-left: 30px !important;" class="m-0 inputTextValue"  >{{this.monthly}}</h5>
                                </div>
                            </div>
             
                            <div  v-if="this.dateTypr == 'daily'" @input="dateV = ''"  style=" border: 1px solid #6AAA69 !important;border-radius: 15px;" class=" mt-4 ">
                                <div  data-status="unOpen" class="textInputDiv"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-calendar inputIcon"></i>
                                        <p @click.self="customInputText($event)" class="m-0">DAILY</p>
                                    </div>
                                    <input   v-model="daily" class="customInput" type="date">
                                    <h5  id="" style="margin-left: 30px !important;" class="m-0 inputTextValue">{{this.daily}}</h5>
                                </div>
                            </div>
                            <div  v-if="this.dateTypr == 'year'" @input="dateV = ''"  style=" border: 1px solid #6AAA69 !important;border-radius: 15px;" class=" mt-4 ">
                                <div  data-status="unOpen" class="textInputDiv"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-calendar inputIcon"></i>
                                        <p @click.self="customInputText($event)" class="m-0">YEARLY</p>
                                    </div>
                                    <input   v-model="yearly" @input="dateV = ''" class="customInput" type="number">
                                    <h5  id="" style="margin-left: 30px !important;" class="m-0 inputTextValue">{{this.yearly}}</h5>
                                </div>
                            </div>

                            <div class="valid_feedback" style="color:red">
                                {{yearlyV}}
                            </div>
                            <div class="valid_feedback" style="color:red">
                                {{dateV}}
                            </div>
                            <div  v-if="this.dateTypr == 'between'" @input="dateV = ''"  style=" border: 1px solid #6AAA69 !important;border-radius: 15px;" class=" mt-4 ">
                                <div    data-status="unOpen" class="textInputDiv"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:0%">
                                        <i class="fa-solid fa-calendar inputIcon"></i>
                                        <p @click.self="customInputText($event)" class="m-0">Form</p>
                                    </div>
                                    <input  v-model="from_date" class="customInput" type="date">
                                    <h5 id="" style="margin-left: 30px !important;" class="m-0 inputTextValue">{{this.from_date}}</h5>
                                </div>
                            </div>
                            <div class="valid_feedback" style="color:red">
                                {{fromV}}
                            </div>
                            <div  v-if="this.dateTypr == 'between'" @input="dateV = ''"  style=" border: 1px solid #6AAA69 !important;border-radius: 15px;" class=" mt-4 ">
                                <div    data-status="unOpen" class="textInputDiv"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:0%">
                                        <i class="fa-solid fa-calendar inputIcon"></i>
                                        <p @click.self="customInputText($event)" class="m-0">To</p>
                                    </div>
                                    <input  v-model="to_date" class="customInput" type="date">
                                    <h5 id="" style="margin-left: 30px !important;" class="m-0 inputTextValue">{{this.to_date}}</h5>
                                </div>
                            </div>
                    
                            <div class="valid_feedback" style="color:red">
                                {{toV}}
                            </div>
                            <div class="valid_feedback" style="color:red">
                                {{toV2}}
                            </div>
                            <div  data-status="unOpen"  class="selectDiv pt-4 mx-1"  data-bs-toggle="modal" data-bs-target="#r-goodsModel"  >
                                <div class="selectInput"  >
                                    <h5 class="m-0">Select Goods</h5>
                                    <div class="d-flex justify-contant-between align-items-center">
                                        <p class="m-0 me-3 selectSmallText"> {{GoodsList.length === JSON.parse(JSON.stringify(this.$store.getters['Goods'])).length ? 'ALL' : GoodsList.length > 0 ? `${GoodsList.length} ITEMS SELECTED`  : 'SELECT'}}</p>
                                        <i  class="fa-solid fa-caret-right"></i>
                                    </div>
                                </div>
                            </div>
                            <div class="valid_feedback" style="color:red">
                                {{goodsV}}
                            </div>
                            <div  data-status="unOpen"  class="selectDiv pt-4 mx-1" data-bs-toggle="modal" data-bs-target="#r-StoresModel"   >
                                <div class="selectInput"  >
                                    <h5 class="m-0">Select Stores</h5>
                                    <div class="d-flex justify-contant-between align-items-center">
                                        <p class="m-0 me-3 selectSmallText"> {{StoresList.length === JSON.parse(JSON.stringify(this.$store.getters['s'])).length ? 'ALL' : StoresList.length > 0 ? `${StoresList.length} ITEMS SELECTED`  : 'SELECT'}}</p>
                                        <i  class="fa-solid fa-caret-right"></i>
                                    </div>
                                </div>
                            </div>    
                            <div class="valid_feedback" style="color:red">
                                {{storeV}}
                            </div>
                            <a hidden  data-bs-dismiss="modal" aria-label="Close" id="ddd"></a>
                                </div>
                                <div style="background: #FFFFFF;box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);border-radius: 15px 15px 0px 0px;height:104px;display: flex;justify-content: center;align-items: center;">
                                    <a @click="generate_report()" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important"   >Generate Report</a>
                                </div>   
                        </div>
                    </div>
            </div>
        </form>       


        <div class="modal fade" id="r-goodsModel" tabindex="-1" aria-labelledby="exampler-goodsModel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" style="max-width:846px !important;">
                    <div class="modal-content" style="height:75% !important;">
                    <div class="modal-body d-flex flex-column" >
                        <div class="closeModel" data-bs-dismiss="modal" aria-label="Close">
                            <img src="../assets/close.png" class="w-50 h-50" alt="">
                        </div>
                        <h3 style="margin-top:16px">New Report</h3>
                        <p style="margin-top:24px">Select Goods</p>
                            <!-- <div class="d-flex headerListPageBtns" style="margin-bottom:10px;width: 75%;justify-content: center;">
                                <button class="secondBtn secondBtnActive" style="margin-right: 10px;">cat1</button>
                                <button class="secondBtn" style="margin-right: 10px;">cat2</button>
                                <button class="secondBtn" style="margin-right: 10px;">cat3</button>
                                <button class="secondBtn" style="margin-right: 10px;">cat4</button>
                            </div> -->
                                <div class="container-list" style="">
                                    <div @click="selectAllGoods()"  class="container-item d-flex justify-content-between" >
                                        <h5 class="container-item-name" >ALL</h5>
                                    </div>
                                    <div @click="choiseGoods(con.id)" v-for="(con,i) in Goods" :key="i" class="container-item d-flex justify-content-between" :class="{'container-item-c': i % 2 === 0}">
                                        <h5 class="container-item-name" :class="{'g-text':GoodsList.includes(con.id)}">{{con.name_en}}</h5>
                                        <img v-if="GoodsList.includes(con.id)" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                                    </div>
                                </div>
                            </div>
                                <div style="background: #FFFFFF;box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);border-radius: 15px 15px 0px 0px;height:104px;display: flex;justify-content: center;align-items: center;">
                                    <button  type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important"  @click="ReportModal()" >Done</button>
                                </div>   
                        </div>
                    </div>
        </div>

        <div class="modal fade" id="r-StoresModel" tabindex="-1" aria-labelledby="exampler-StoresModel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" style="max-width:846px !important;">
                    <div class="modal-content" style="height:75% !important;">
                    <div class="modal-body d-flex flex-column" >
                        <div class="closeModel" data-bs-dismiss="modal" aria-label="Close">
                            <img src="../assets/close.png" class="w-50 h-50" alt="">
                        </div>
                        <h3 style="margin-top:16px">New Report</h3>
                        <p style="margin-top:24px">Select Store </p>
                           
                                <div class="container-list" style="">
                                    <div @click="selectAllStore()"  class="container-item d-flex justify-content-between" >
                                        <h5 class="container-item-name" >ALL</h5>
                                    </div>
                                    <div @click="choiseStores(con.id)" v-for="(con,i) in s" :key="i" class="container-item d-flex justify-content-between" :class="{'container-item-c': i % 2 === 0}">
                                        <h5 class="container-item-name" :class="{'g-text':StoresList.includes(con.id)}">{{con.name_en}}</h5>
                                        <img v-if="StoresList.includes(con.id)" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                                    </div>
                                </div>
                            </div>
                                <div style="background: #FFFFFF;box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);border-radius: 15px 15px 0px 0px;height:104px;display: flex;justify-content: center;align-items: center;">
                                    <button  type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important" @click="ReportModal()" >Done</button>
                                </div>   
                        </div>
                    </div>
        </div>

        
    </div>
</template>

<script>

/* eslint-disable */  import functions from  "../assets/js/js.js";
import { mapActions,mapGetters  } from 'vuex';
import _axios from '../axios'
import ReportTabel from "./ReportTabel.vue";

export default {
name: 'ReportsPage',
components: {

},
data() {
return {
    monthly:'',
    daily:new Date().toISOString().slice(0, 10),
    yearly:'',
    yearlyV:'',
    monthlyV:'',
    date:'',
    from_date:'',
    to_date:'',
    all_store:false,
    all_goods:false,
    dateTypr:'month',
    store_list:['store 1','store 2'],
    store:'',
    GoodsList:[],
    StoresList:[],
    dateV:'',
    goodsV:'',
    storeV:'',
    fromV:'',
    toV:'',
    report:'',
    
 }
},
methods:{
    change(type){
        this.dateTypr = type
    },
    customInput(el){
        functions.customInputa(el)
    },
    customInputText(el){
        el.target.parentElement.parentElement.click()
    },
    customSelectP(el){
        el.target.parentElement.parentElement.click()
    },
    openSelect(el){
        functions.openSelect(el)
    },
    Sucond_Btn(){
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='none';
    },
   
    First_Btn(){
    const First_Btn = document.getElementById('First_Btn')
    First_Btn.style.display='flex';
    First_Btn.querySelector('p').innerText='NEW REPORTS'
    First_Btn.addEventListener('click',function() {document.getElementById('ReportsModalBtn').click()})
    
},
ReportModal(){

    document.getElementById('ReportsModalBtn').click();

},
    ActiveDateBtn(el){
          var TypeDateBtn= document.getElementsByClassName('TypeDateBtn')
          for(var c=0; c<TypeDateBtn.length; c++){
            TypeDateBtn[c].setAttribute('class', 'secondBtn TypeDateBtn btn-text');
          }
          el.currentTarget.className = "secondBtnActive TypeDateBtn btn-text"; 
      },

      choiseGoods(con){
    this.goodsV = ''
    this.GoodsList.includes(con)  ? this.GoodsList.splice(this.GoodsList.indexOf(con), 1) : this.GoodsList.push(con)
},
choiseStores(con){
    this.storeV = ''
    this.StoresList.includes(con)  ? this.StoresList.splice(this.StoresList.indexOf(con), 1) && (this.all_store = false) : this.StoresList.push(con)
},
    selectAllStore(){
        console.log('[[[[[[[[[[[[[[[[[[[[[[[[[[[[[[[[[]]]]]]]]]]]]]]]]]]]]]]]]]]]]]]]]]')
        this.all_store = true
        this.storeV = ''
        for ( var i in JSON.parse(JSON.stringify(this.$store.getters['s']))){
            this.StoresList.includes(JSON.parse(JSON.stringify(this.$store.getters['s']))[i].id) ? '' : this.StoresList.push(JSON.parse(JSON.stringify(this.$store.getters['s']))[i].id)
        }
    },
    selectAllGoods(){
        this.all_goods = true
        this.goodsV = ''
        for ( var i in JSON.parse(JSON.stringify(this.$store.getters['Goods']))){
            this.GoodsList.includes(JSON.parse(JSON.stringify(this.$store.getters['Goods']))[i].id) ? '' : this.GoodsList.push(JSON.parse(JSON.stringify(this.$store.getters['Goods']))[i].id)
        }

    },
    validation(){
        var v = true
        if (this.dateTypr !== 'between'){
            var d = this.dateTypr === 'daily' ? this.daily : this.dateTypr === 'month' ? this.monthly : this.dateTypr === 'year' ? this.yearly : ''
            if(d.length <= 0 ){
                v = false
                this.dateV = 'IS_REQUIRED'
            }
        }
        if (this.dateTypr === 'month'){
            if( this.monthly > 12 || this.monthly < 1  ){
                v = false
            }
        }
        if (this.dateTypr === 'year'){
            if (this.yearly > new Date().getFullYear()){
                v = false
            }
        }
        if (this.dateTypr === 'between'){
            if(this.from_date > this.to_date){
                v = false
            }
            if ( this.to_date > new Date().toISOString().slice(0, 10)){
                v = false
            }
        }
        else{
            if(this.dateTypr=== 'between' && !this.from_date  ){
                this.fromV = 'IS_REQUIRED'
                v = false
            }
            if(this.dateTypr=== 'between' && this.from_date >  new Date().toISOString().slice(0, 10) ){
                v = false
            }
            if(this.dateTypr=== 'between' && this.to_date >  new Date().toISOString().slice(0, 10)){
                v = false
            }
            if(this.dateTypr=== 'between' && !this.to_date ){
                this.toV = 'IS_REQUIRED'
                v = false
            }
        }
        if(this.GoodsList.length <= 0 ){
            v = false
            this.goodsV = 'IS_REQUIRED'
        }
        if(this.StoresList.length <= 0 ){
            v = false
            this.storeV = 'IS_REQUIRED'
        }
        return v
    },
    generate_report(){
        if(this.validation()){
            document.getElementById('ddd').click()
            var  date = this.dateTypr === 'daily' ? this.daily : this.dateTypr === 'month' ? this.monthly : this.dateTypr === 'year' ? this.yearly : ''
            var  dateTypr = this.dateTypr
            if(date = this.dateTypr !== 'between'){
                var payload = {
                    "stores": (this.all_store ? 'all' : this.StoresList),
                    "products": (this.all_goods ? 'all' : this.GoodsList),
                     dateTypr: date
                }
            }else{
                var payload = {
                    "stores": (this.all_store ? 'all' : this.StoresList),
                    "products": (this.all_goods ? 'all' : this.GoodsList),
                    "from_date":this.from_date,
                    "to_date":this.to_date
                }
            }
                console.log(payload)
                _axios.post('/api/reports/',payload, { headers: {
                    'Authorization': `token ${window.localStorage.token}`}
                  })
                .then(response => {
                    this.$store.state.report_date = response.data
                    this.$router.push({ name: 'ReportTabel' })
                })
                .catch(error => {
                    console.log('Error')
                    console.error(error.response.data);
                });
        }
    },
    click_from_date(){
        document.getElementById('form-date').click()
    },
    get_reports(){
        _axios.get('/api/reports/user', { headers: {
                    'Authorization': `token ${window.localStorage.token}`}
                  })
                .then(response => {
                    console.log(response)
                    this.report = response
                })
                .catch(error => {
                    console.log('Error')
                    console.error(error.response.data);
                });},
    get_pdf(id){
        _axios.post('/api/reports/',{
                      "id": id
                  }, { headers: {
                    'Authorization': `token ${window.localStorage.token}`,} ,
                        responseType: 'blob'
                  })
                .then((response) => {
                    window.open(URL.createObjectURL(response.data));
                })
                .catch(error => {
                    console.error(error);
                });
    },

},
watch:{
    monthly:function(value){
        console.log(value)
        if (value > 12){
            this.monthlyV = 'month more than 12' 
        }
        else if(value < 1){
            this.monthlyV = 'month is less than 1' 
        }else{
            this.monthlyV = '' 

        }
    },

    to_date:function(value){
        if (value < this.from_date){
            this.toV = '"From Date" Greater "Than To Date"'
        }else{
            this.toV = ""
        }
        if (value > new Date().toISOString().slice(0, 10)){
            this.toV2 = "Your date is greater than today's date"
        }else{
            this.toV2 = ""
        }
    },
    from_date:function(value){
        if (value > new Date().toISOString().slice(0, 10)){
            this.fromV = "Your date is greater than today's date"
        }else{
            this.fromV = ""
        }
    },
    yearly:function(value){
        if (value > new Date().getFullYear()){
            this.yearlyV="year is greater than year's date"
        }else{
            this.yearlyV=''
        }
        
    },
    },
computed:{
    ...mapGetters(['s','errors','Goods'])
},
created(){
    document.getElementById('navaya').style.display = 'block'
    document.getElementById('h1h').style.display = 'flex'
    document.getElementById('h2h').style.display = 'none'
    this.First_Btn()
    this.Sucond_Btn()
    this.$store.dispatch('Getstore')
    this.$store.dispatch('GetGood')
    this.get_reports()

},
mounted(){

    if ( !window.localStorage.token){
        this.$router.push({name:'Login'})
    }
    setTimeout( function(self) {
        self
        this.selectAllStore()
        this.selectAllGoods()
    }.bind(this),1000)

}
}
</script>

<style scoped>

.info{
    margin-right:5.5%
}
.pdf-text{
    font-family: 'Poppins';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 21px;
/* identical to box height */

text-align: right;

color: #6AAA69;
}
</style>

