import _axios from '../axios'
import router from '../router';

export default({
    state:{
       InfoList:[],
       InfoListSelected:[],
       errors:[],
    },
    getters: {
        Orders: state  => state.InfoList,
        OrdersSelect: state  => state.InfoListSelected,
        // OrdersSelect: state  => state.InfoList.map(function(item) {
        //   item["selected"] = false;
        //   return item;
        // }),
        errors: state  => state.errors,
    },
    mutations:{
        Orders(state, data) {
            state.InfoList = data;
          },
        OrdersSelected(state, data) {
          state.InfoListSelected = data.map(function(item) {
            item["selected"] = false;
            return item;
            });
        },
    },
    actions:{
        SerchOrders({ commit },query){ 
          console.log(query)
            _axios.get('/api/search_container', { headers: {
                'Authorization': `token ${window.localStorage.token}`},
                params:{'query':query.qurey}
              })
            .then(response => {
              commit('Orders', response.data);
              commit('OrdersSelected', response.data);
              
          })
          .catch(error => {
              console.error(error);
          });
        },
        Filter({ commit },qurey){ 
          _axios.get('/api/containers/',{ 
            headers: {
            'Authorization': `token ${window.localStorage.token}`},
             params: {
                'current_status': qurey.qurey,
            }})
            .then(response => {
              commit('Orders', response.data);
              commit('OrdersSelected', response.data);
            })
            .catch(error => {
                console.error(error.response);
            });
        },
        GetOrders({ commit }){ 
            _axios.get('/api/containers', { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
              // console.log(response.data)
              commit('Orders', response.data);
              commit('OrdersSelected', response.data);
          })
          .catch(error => {
              console.error(error);
              if(error.message === "Request failed with status code 401"){
                router.push({ name: 'Login' });
              }else if(error.message === "Request failed with status code 403"){
                router.push('/404-page');
              }
          });
        },
          AddOrder({dispatch ,state},payload){
            _axios.post('/api/containers',payload, { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
                response
                dispatch('GetOrders')
                // document.getElementById('storeResponsText').innerText = 'Delete Successfully'
                document.getElementById('modalBtn').click()
            })
            .catch(error => {
              
              for(var i in error.response.data){
                state.errors.push({e_key:i,e_value:error.response.data[i][0]})
              }
              document.getElementById('EmodalBtn').click()
            });
        },
          EditOrder({dispatch ,state}, payload){
            console.log(payload)
            state
            dispatch
            console.log('""""""""""""""""""""""""""""')
            _axios.put(`/api/containers/${payload.id}/`,payload.data, { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
                response
                dispatch('GetOrders')
                document.getElementById('modalBtn').click()
            })
            .catch(error => {
              console.log(error)
              for(var i in error.response.data){
                state.errors.push({e_key:i,e_value:error.response.data[i][0]})
              }
              document.getElementById('EmodalBtn').click()
                document.getElementById('EmodalBtn').click()
            });
        },
    },
  })