import _axios from '../axios'
// import Router from 'vue-router';
import router from '../router'

export default({
    state:{
        user:'',
        all:'',
    },
    getters: {
        user: state  => state.user.split('@')[0],
    },
    mutations:{
        login(state,UserInfo){
            state
            UserInfo
            // console.log(UserInfo)
        }
    },
    
    actions:{
        login({ commit,state },payload){
            _axios.post('/api/login/',payload)
            .then(response => {
                // console.log(response.data.token)
                window.localStorage['token'] = response.data.token
                window.localStorage['all'] = response.data.all
                window.localStorage['UserName'] = payload.email
                state.user = payload.email
                console.log(response.data.all)
                state.all = response.data.all
                
                router.push({ name: 'OrderContainerList' })
            })
            .catch(error => {
                document.getElementById('loginerrorbtn').click()
                console.error(error);
            });
            commit('login',payload)
        },
    },
  })