<template>
    <div class="AddPage" style="">
        <div class="selects" style="flex-wrap:nowrap;padding: 0 20px;">  
            <div  data-status="unOpen"  class="selectDiv pt-4 mx-1"  @click="openSelect($event)"  >
                <div class="selectInput"  >
                    <h5 class="m-0">Current Proccess</h5>
                    <div class="d-flex justify-contant-between align-items-center">
                        <p class="m-0 me-2 selectSmallText"><span style="color:red !important ; margin:0 5px" >{{!current_proccess ? current_proccessValid  : ''}}</span>  {{current_proccess ? current_proccess.value : 'Select'}}</p>
                        <i  class="fa-solid fa-caret-down"></i>
                    </div>
                </div>
                <div  class="option selectOption" >
                    <div class="optionValue d-flex justify-content-between align-items-center" v-for="(value,i) in current_proccess_list " v-bind:key="i" @click="this.current_proccess = value" :class="{'wihteBlueC':i % 2 === 0 }" >
                        <h5  class="m-0" :class="{'greenColor':value.value === this.current_proccess }" >{{value.key}}</h5>
                        <img v-if="value.value === this.current_proccess.value" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                    </div>
                </div>
            </div>
            <div  data-status="unOpen"  class="selectDiv pt-4 mx-1"  @click="openSelect($event)"  >
                <div class="selectInput"  >
                    <h5 class="m-0">Choose Store</h5>
                    <div class="d-flex justify-contant-between align-items-center">
                        <p class="m-0 me-2 selectSmallText" style="white-space: nowrap;"><span style="color:red !important ; margin:0 5px" >{{!store ? storeValid : ''}}</span> {{store ? store.name_en : 'Select'}}</p>
                        <i  class="fa-solid fa-caret-down"></i>
                    </div>
                </div>
                <div  class="option selectOption" >
                    <div class="optionValue d-flex justify-content-between align-items-center" v-for="(value,i) in s " v-bind:key="i" @click="this.store = value" :class="{'wihteBlueC':i % 2 === 0 }" >
                        <h5  class="m-0" :class="{'greenColor':value.name_en === this.store.name_en }" >{{value.name_en}}</h5>
                        <img v-if="value.name_en === this.store.name_en" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                    </div>
                </div>
            </div>
            <div  data-status="unOpen"  class="selectDiv pt-4 mx-1"  @click="openSelect($event)"  >
                <div class="selectInput"  >
                    <h5 class="m-0">Choose Driver</h5>
                    <div class="d-flex justify-contant-between align-items-center">
                        <p class="m-0 me-2 selectSmallText" style="white-space: nowrap;"><span style="color:red !important ; margin:0 5px" >{{!driver ? driverValid  : ''}}</span>  {{driver ? driver.name_en : 'Select'}}</p>
                        <i  class="fa-solid fa-caret-down"></i>
                    </div>
                </div>
                <div  class="option selectOption" >
                    <div class="optionValue d-flex justify-content-between align-items-center" v-for="(value,i) in Dri " v-bind:key="i" @click="this.driver = value" :class="{'wihteBlueC':i % 2 === 0 }" >
                        <h5  class="m-0" :class="{'greenColor':value === this.driver.name_en }" >{{value.name_en}}</h5>
                        <img v-if="value.name_en === this.driver.name_en" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                    </div>
                </div>
            </div>
            <!-- <div v-if="current_proccess.value === 'Outgoing' " data-status="unOpen"  class="selectDiv pt-4 mx-1"  @click="openSelect($event)"  > -->
            <div  data-status="unOpen"  class="selectDiv pt-4 mx-1"  @click="openSelect($event)"  >
                <div class="selectInput"  >
                    <h5 class="m-0">Delivery Time</h5>
                    <div class="d-flex justify-contant-between align-items-center">
                        <p class="m-0 me-2 selectSmallText" style="white-space: nowrap;"><span style="color:red !important ; margin:0 5px" >{{!delivery_date ? delivery_datV  : ''}}</span>  {{delivery_date ? delivery_date: 'Select'}}</p>
                        <i  class="fa-solid fa-caret-down"></i>
                    </div>
                </div>
                <div  class="option selectOption " >
                    <div class="d-flex justify-content-center align-items-center p-2">
                        <input type="date" v-model="delivery_date" style="border:none">
                    </div>
                    <!-- <div class="optionValue d-flex justify-content-between align-items-center" v-for="(value,i) in Dri " v-bind:key="i" @click="this.driver = value" :class="{'wihteBlueC':i % 2 === 0 }" >
                        <h5  class="m-0" :class="{'greenColor':value === this.driver.name }" >{{value.name}}</h5>
                        <img v-if="value.name === this.driver.name" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                    </div> -->
                </div>
            </div>
            <button hidden id="selectGoods" data-bs-toggle="modal" data-bs-target="#OrderModel">SELECT GOODS 
            </button>
            <button class="selectGoods mx-1 mt-4" @click="selectValedation()">SELECT GOODS 
            </button>
        </div>

        <div class="tebel-info" style="width:100%;border:none">
            <div class="listDiv" style="">
                <div class="contanetList" >
                    <div style="padding:25px;height:134px;gap:12%" class="orderDiv mt-4 d-flex " v-for="(order,i) in submetList"  v-bind:key="i">
                        <div class="d-flex flex-column justify-content-between h-100 info_sction">
                            <div class="d-flex gap-2 align-items-center OrdersListInfo">
                                <i style="font-size:20px" class="fa-solid fa-box"></i>
                                <p style="font-size: 12px;" class="m-0">Goods Name</p>
                            </div>
                            <div>
                                <div class="goodsNameInput d-flex align-items-center p-2" type="text">{{order.product.name_en}}</div>
                            </div>
                        </div>
                        <div class="d-flex flex-column justify-content-between h-100 info_sction">
                            <div class="d-flex gap-2 align-items-center OrdersListInfo">
                                <i style="font-size:20px" class="fa-solid fa-plus-minus  "></i>
                                <p style="font-size: 12px;" class="m-0">Needed Amount</p>
                            </div>
                            <div>
                                <div class="goodsNameInput d-flex justify-content-center align-items-center" type="text">
                                    <div class="cul" style="cursor:pointer" @click="plus($event)">+</div>
                                    <input value="1" class="goodsNameInput text-center" style="width:50%;outline:none" min="1" type="number" name="" :id="`amount${i}`">
                                    <div class="cul" style="cursor:pointer" @click="minus($event)">-</div>
                                </div>
                            </div>
                        </div>
                        <div class="d-flex flex-column justify-content-between h-100 info_sction">
                            <div class="d-flex gap-2 align-items-center OrdersListInfo">
                                <i style="font-size:20px" class="fa-solid fa-calendar"></i>
                                <p style="font-size: 12px;" class="m-0">Needed Date</p>
                            </div>
                            <div>
                                <input class="goodsNameInput text-center" type="date" :id="`date${i}`">
                            </div>    
                        </div>
                    </div>
                    <div v-if=" submetList.length > 0 " style="height:104px;display: flex;justify-content: center;align-items: center;" class="mt-3">
                        <button type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important" data-bs-toggle="modal" data-bs-target="#ConfirmOrderModel" >Submit Order</button>
                    </div>
                </div>
                <div class="b" style="height:200px;display:none;">
                </div>
            </div>
        </div>

        <div class="modal fade" id="ConfirmOrderModel" tabindex="-1" aria-labelledby="ConfirmOrderModelLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" style="height:556px !important; max-width:656px !important;">
                <div class="modal-content" >
                    <div class="modal-body d-flex flex-column p-5" style="background-color: #F1F1F1 !important;">
                        <img src="../assets/qu.png" alt="">
                        <h3 style="margin-top:16px;margin-bottom: 0;">Confirm Order</h3>
                        <p style="margin-top:24px">Do you really want to submit this order?</p>
                        <button v-if="!spin" class="btn prymaryBtn w-50 " style="color:white;height:50px;margin-top:25px" @click="SupmitOrder()" >Yes, Submit</button>
                        <div v-else class="spi"></div>
                        <button class="btn  w-50 g-text" style="color:white;height:50px;margin-top:15px;background: #FFFFFF;color: #6AAA69  !important;    border-radius: 15px;border: none; padding: 15px 22px;" data-bs-dismiss="modal" aria-label="Close" >Cancel</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal fade" id="OrderModel" tabindex="-1" aria-labelledby="exampleOrderModel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" style='max-width:846px !important;'>
                    <div class="modal-content" style="height:75% !important;">
                    <div class="modal-body d-flex flex-column" >
                        <div class="closeModel" data-bs-dismiss="modal" aria-label="Close">
                            <img src="../assets/close.png" class="w-50 h-50" alt="">
                        </div>
                        <h3 style="margin-top:16px">OutGoing Order</h3>
                        <p style="margin-top:24px">Select Goods to transfer it to store  “Store Name”</p>
                            <!-- <div class="d-flex headerListPageBtns" style="margin-bottom:10px;width: 75%;    justify-content: center;">
                                <button class="secondBtn secondBtnActive" style="margin-right: 10px;">cat1</button>
                                <button class="secondBtn" style="margin-right: 10px;">cat2</button>
                                <button class="secondBtn" style="margin-right: 10px;">cat3</button>
                                <button class="secondBtn" style="margin-right: 10px;">cat4</button>
                            </div> -->
                                <div class="container-list" style="">
                                    <div @click="choiseGood(con.product.name_en),GoodsList(con)" v-for="(con,i) in the_goods" :key="i" class="container-item d-flex justify-content-between" :class="{'container-item-c': i % 2 === 0}">
                                        <h5 class="container-item-name" :class="{'g-text':goods_container.includes(con.product.name_en)}">{{con.product.name_en}}</h5>
                                        <img v-if="goods_container.includes(con.product.name_en)" src="../assets/check.png" style="width:1.5rem;height:1.5rem">
                                    </div>
                                </div>
                            </div>
                                <div style="background: #FFFFFF;box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);border-radius: 15px 15px 0px 0px;height:104px;display: flex;justify-content: center;align-items: center;">
                                    <button  type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important" data-bs-dismiss="modal" aria-label="Close" >Done</button>
                                    <!-- <button @click="this.$router.push({ name: 'SubmitOrder' })" type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important" data-bs-dismiss="modal" aria-label="Close" >Done</button> -->
                                </div>   
                        </div>
                    </div>
        </div>

        <!-- <button hidden @click="this.$router.push({ name: 'Orders' })" id="GoAddOrder"></button> -->
        <button hidden id="successModelBtn" data-bs-toggle="modal" data-bs-target="#successModel"></button>

        <div class="modal fade" id="successModel" tabindex="-1" aria-labelledby="successModelLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" style="height:556px !important; max-width:656px !important;">
                <div class="modal-content" >
                    <div class="modal-body d-flex flex-column p-5" style="background-color: #F1F1F1 !important;">
                        <img src="../assets/bigCheck.png" alt="">
                        <h3 style="margin-top:16px;margin-bottom: 0;">Request Sent Successfully</h3>
                        <p style="margin-top:24px">Your request was done successfully</p>
                        <button class="btn  w-50 g-text" style="color:white;height:50px;margin-top:15px;background: #FFFFFF;color: #6AAA69  !important;    border-radius: 15px;border: none; padding: 15px 22px;" data-bs-dismiss="modal" aria-label="Close" @click="this.$router.push({ name: 'OrderContainerList' })" >DONE</button>
                    </div>
                </div>
            </div>
        </div>
        <button hidden id="erorModelBtn" data-bs-toggle="modal" data-bs-target="#erorModel"></button>
        <div class="modal fade" id="erorModel" tabindex="-1" aria-labelledby="successModelLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" style="height:556px !important; max-width:656px !important;">
                <div class="modal-content" >
                    <div class="modal-body d-flex flex-column p-5" style="background-color: #F1F1F1 !important;">
                        <i style="color:red;font-size:xx-large" class="fa-solid fa-circle-exclamation"></i>
                        <h3 style="margin-top:16px">ERROR</h3>
                        <p style="margin-top:24px">{{response_error}}</p>
                        <button class="btn  w-50 g-text" style="color:white;height:50px;margin-top:15px;background: #FFFFFF;color: #6AAA69  !important;    border-radius: 15px;border: none; padding: 15px 22px;" data-bs-dismiss="modal" aria-label="Close"  >DONE</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
    
</template>

<script>
/* eslint-disable */  import functions from  "../assets/js/js.js";
import { mapActions,mapGetters  } from 'vuex';
import _axios from '../axios'
export default {
name: 'OrdersPage',
components: {
},
data() {
return {
    response_error:'',
    spin:false,
    current_status:'',
    CurrentStatusList:['Shipping','In the sea','Arrived','With Clearance Agent','Needs attention','Finished'],
    containers:['container 1','container 2','container 3','container 4','container 5','container 6'],
    goods_container:[],
    current_proccess_list:[{key:'Incoming Order',value:'Incoming'},{key:'Outgoing Order',value:'Outgoing'}],
    current_proccess:'',
    store:'',
    driver:'',
    delivery_date:'',
    delivery_datV:'',
    current_proccessValid:'',
    driverValid:'',
    storeValid:'',
    the_goods:[],
    submetList:[],
    goodsList:[],
    data:{
        "order_type": '',
        "store":'' ,
        "driver_no":'',
        "delivery_date":'',
        "products": [

        ]
    },

    }
},
methods:{
    customInput(el){
        functions.customInputa(el)
    },
    customInputText(el){
        el.target.parentElement.parentElement.click()
    },
    customSelectP(el){
        el.target.parentElement.parentElement.click()
    },
    openSelect(el){
        functions.openSelect(el)
    },
    Sucond_Btn(){
        // document.getElementById('Sucond_Btn')
        // Sucond_Btn.style.display ='none';
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='none';
    },
    First_Btn(){
    const First_Btn = document.getElementById('First_Btn')
    First_Btn.style.display='flex';
    First_Btn.querySelector('p').innerText='SAVE ORDR'
    First_Btn.addEventListener('click',function() {document.getElementById('GoAddOrder').click();})
},
choiseGood(con){
    this.goods_container.includes(con)  ? this.goods_container.splice(this.goods_container.indexOf(con), 1) : this.goods_container.push(con)
},
GoodsList(g){
    this.submetList.includes(g)  ? this.submetList.splice(this.submetList.indexOf(g), 1) : this.submetList.push(g)
    // console.log(document.getElementById(`amount${this.goodsList.length}`))


},
SupmitOrder(){
    var c = 0
    this.spin = true
    console.log(document.getElementById(`amount${c}`).value)
    this.data.products=[]
    for( var  i of this.submetList){
        console.log('----------------')
        console.log(c)
        var qu = document.getElementById(`amount${c}`).value
        var da = document.getElementById(`date${c}`).value
        this.data.products.push(
            {
                "name_ar":i.product.name_ar,
                "quantity": qu,
                "needed_date":da
            }
        )
    c++
    }
    c= 0
    console.log(JSON.parse(JSON.stringify(this.data)))
    console.log('====================')

    _axios.post('/api/orders/',this.data,{ headers: {
                'Authorization': `token ${window.localStorage.token}`}
              }).then(response => {
                console.log(response)
                console.log('=========')
                if(response.data.status === true){
                    document.getElementById('successModelBtn').click()
                }else{
                    this.response_error = response.data.result
                    document.getElementById('erorModelBtn').click()
                }
                  this.spin = false
                }
                ).catch(error => {
                    console.log('Error')
                    console.error(error.response.data);
                    this.spin = false
            });
},

GetOrder(){
        console.log('response')
        _axios.get('/api/orders/', { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
        .then(response => {    
            this.orders = response.data.result
            console.log(this.orders)
            console.log('==============')
            }).catch(error => {console.log(error)})
    },
    selectValedation(){
        var selectGoodVal = true
        if (!this.current_proccess){
            this.current_proccessValid = 'required'
            selectGoodVal = false
        }
        if (!this.store){
            this.storeValid = 'required'
            selectGoodVal = false
        }
        if (!this.driver){
            this.driverValid = 'required'
            selectGoodVal = false
        }
        if (!this.delivery_date){
            this.delivery_datV = 'required'
            selectGoodVal = false
        }
        console.log(selectGoodVal)
        if (selectGoodVal){
            this.data.order_type = this.current_proccess.value
            this.data.store = this.store.name_en
            this.data.driver_no = this.driver.id_no
            this.data.delivery_date = this.delivery_date
            console.log(this.data)
            _axios.get(`/api/stores/${this.store.id}/quantities`, { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              }).then(response => {
                console.log('=============')
                console.log(response.data)
                this.the_goods = response.data
                document.getElementById('selectGoods').click();
              })
        }
    },
    plus(el){
    el.target.nextElementSibling.value++
    },
    minus(el){
        if(el.target.previousElementSibling.value > 1 ){
            el.target.previousElementSibling.value--
        }
    },
},

computed:{
    ...mapGetters(['Dri','s','errors','Goods']),
},
created(){
    document.getElementById('navaya').style.display = 'block'
    document.getElementById('h1h').style.display = 'flex'
    document.getElementById('h2h').style.display = 'none'

    this.First_Btn()
    this.Sucond_Btn()
    this.$store.dispatch('Getstore')
    this.$store.dispatch('GetDrivers')
    this.$store.dispatch('GetGood')
    this.GetOrder()
},
mounted(){
    if ( !window.localStorage.token){
        this.$router.push({name:'Login'})
    }
}
}
</script>

<style >
.container-list{
    width:75%;
    height:500px;
    overflow: auto;
}
.container-item{
    width: 100%;
    padding:18px 46px ;
}
.container-item-c{
    background: rgba(111, 149, 181, 0.1);
}
.container-item-name{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
}
.g-text{
    color:#6AAA69 ;
}
.spi {
  border: 3px solid hsla(185, 100%, 62%, 0.2);
  border-top-color: #6AAA69  ;
  border-radius: 50%;
  width: 3em;
  height: 3em;
  animation: spinn 1s linear infinite;
}

@keyframes spinn {
  to {
    transform: rotate(360deg);
  }
}
</style>

