import _axios from '../axios'
import router from '../router';
export default({
    state:{
       HInfoList:[]
    },
    getters: {
        histories: state  => state.HInfoList
    },
    mutations:{
        Gethistories(state, data) {
            state.HInfoList = data;
          },
    },
    actions:{
        Addhistories(contex,payload){
            console.log(window.localStorage.token)
            _axios.post('/api/histories',payload, { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
                response
                contex.dispatch('Gethistories')
            })
            .catch(error => {
                console.log('Error')
                console.error(error.response.data);
            });
        },
        Gethistories({ commit }){ 
            console.log(window.localStorage.token)
            _axios.get('/api/histories', { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
              console.log(response.data);
              commit('Gethistories', response.data);
          })
          .catch(error => {
            if(error.message === "Request failed with status code 403"){
                router.push('/404-page');
              }
              console.error(error);
          });}
    },
  })