<template>
    
    <div class="tebel-info">
        <div class="listDiv" style="">
            <div class="headerListPage flex-column">
                <div>
                    <h2 class="pageLabel m-0">Report Details</h2>
                </div>
                <button class="selectGoods mx-1 mt-4" style="width:355px" @click="save_pdf(report.id)" >SAVE AS PDF</button>
            </div>
            <div class="contanetList d-flex justify-content-center" >
                <table class="table table-bordered" style="width:729px;border:solid black 0.5px;white-space: nowrap;">
                    <thead>
                      <tr>
                        <th scope="col" class="tabel-headers" style="width: 5%;text-align: center;">IN</th>
                        <th scope="col" class="tabel-headers" style="width: 10%;text-align: center;">ID</th>
                        <th scope="col" class="tabel-headers" style="width: 45%;text-align: left;">GOODS NAME</th>
                        <th scope="col" class="tabel-headers" style="width: 10%;text-align: center;">Incoming</th>
                        <th scope="col" class="tabel-headers" style="width: 10%;text-align: center;">Outgoing</th>
                        <th scope="col" class="tabel-headers" style="width: 10%;text-align: center;">Quantity</th>
                      </tr>
                    </thead>
                    <tbody v-for="(company,i) in report.data" :key="i">
                        <tr >
                          <td colspan="6" class="" style="border-right:none;"><strong>{{company.store.name_en}}</strong></td>
                        </tr>
                        <tr v-for="(product,c) in company.products" :key="c">
                          <th scope="row " class="text-center record-bg">{{c++}}</th>
                          <td class="text-center record-bg">{{product.product.product_sku}}</td>
                          <td class=" record-bg">{{product.product.name_en}}</td>
                          <td class="text-center record-bg">{{product.incoming}}</td>
                          <td class="text-center record-bg">{{product.outgoing}}</td>
                          <td class="text-center record-bg">{{product.quantity}}</td>
                        </tr>         
                    </tbody>
                  </table>
            </div>
            <div class="b" style="height:200px;display:none;">
            </div>
        </div>
    
<a hidden id="Reportpage" @click="$router.push({name:'Reports'})"></a>
      
   

    </div>
</template>

<script>

/* eslint-disable */  import functions from  "../assets/js/js.js";
import { mapActions,mapGetters  } from 'vuex';
import _axios from '../axios'

export default {
name: 'ReportsPage',
components: {

},
data() {
return {

 }
},
methods:{

    Sucond_Btn(){
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='none';
    },
   
    First_Btn(){
    const First_Btn = document.getElementById('First_Btn')
    First_Btn.style.display='flex';
    First_Btn.querySelector('p').innerText='NEW REPORTS'
    First_Btn.addEventListener('click',function() {document.getElementById('Reportpage').click();})
},

save_pdf(id){
  console.log(id)
        _axios.post('/api/reports/',{
                      "id": id
                  }, { headers: {
                    'Authorization': `token ${window.localStorage.token}`,} ,
                        responseType: 'blob'
                  })
                .then((response) => {
                    window.open(URL.createObjectURL(response.data));
                })
                .catch(error => {
                    console.error(error);
                });
},

},
computed:{
    ...mapGetters(["report"])
},
created(){
    document.getElementById('navaya').style.display = 'block'
    document.getElementById('h1h').style.display = 'flex'
    document.getElementById('h2h').style.display = 'none'
    this.First_Btn()
    this.Sucond_Btn()
    this.$store.dispatch('Getstore')
},
mounted(){
    if ( !window.localStorage.token){
        this.$router.push({name:'Login'})
    }

}
}
</script>

<style scoped>
.tabel-headers{
    background-color:#6F95B5;
    border: 0.5px solid #000000;
    color:white;
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 20px;
/* identical to box height */


color: #FFFFFF;
}
.record-bg{
    background-color: #FFFFFF;
}


</style>