<template>

    <div class="tebel-info">
        <div class="goodsDetails" style="">
            <div class="headerListPage">
                <div>
                    <h2 class="pageLabel m-0">GOODS DETAILS</h2>
                </div>
          
            </div>
            <div class="mt-5 ">
                <div class="GinfoDiv row align-items-center" style="background-color:#003F5E !important;">
                    <div class="col-3 text-center"><p style="color:white !important" class="m-0 TIG">Goods Name / English</p></div>
                    <div class="col-3 text-center"><p style="color:white !important" class="m-0 TIG">Goods Name / Arabic</p></div>
                    <div class="col-3 text-center"><p style="color:white !important" class="m-0 TIG">Goods Code</p></div>
                    <div class="col-3"><p></p></div>
                </div>
                <div v-for="(good,i) in Goods" :key="i" class="GinfoDiv row align-items-center"  >
                    <div class="col-3 text-center"><p class="m-0 TIG">{{good.name_en}}</p></div>
                    <div class="col-3 text-center"><p class="m-0 TIG">{{good.name_ar}}</p></div>
                    <div class="col-3 text-center"><p class="m-0 TIG">{{good.product_sku}}</p></div>
                    <div class="col-3 d-flex gap-3 justify-content-center">
                        <img class='editIcon' style="width:50px;cursor: pointer;" @click="EditGoodBtn({'name_en':good.name_en,'name_ar':good.name_ar,'product_sku':good.product_sku,'id':good.id})" src="../assets/edit.png" alt="">
                        <img class='editIcon' style="width:50px;cursor: pointer;" @click="DeleteGood(good.id)" src="../assets/delete.png" alt="">
                    </div>
                </div>
            </div>
            <button @click="AddGoodBtn()" hidden id="addGoodsModalBtn" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#GoodsModalBtn">
            </button>
            <form  @submit.prevent="this.requestType ==='ADD' ?AddGood(this.GoodInformation):EditGood({'id':this.id,'info':this.GoodInformation})">
            <div class="modal fade" id="GoodsModalBtn" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" style="height:529px !important;max-width:846px !important;">
                    <div class="modal-content" style=" ">
                    <div class="modal-body d-flex flex-column" >
                        <div class="closeModel" data-bs-dismiss="modal" aria-label="Close">
                            <img src="../assets/close.png" class="w-50 h-50" alt="">
                        </div>
                        <h3 style="margin-top:16px">NEW GOODS</h3>
                        <p style="margin-top:24px">Insert Data To Generate New Clearance Good</p>
               
                                <div  data-status="unOpen" class="textInputDiv textInputDivBorder mt-3"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-tags inputIcon"></i>
                                        <p @click.self="customInputText($event)" style="width:auto;white-space: nowrap;" class="m-0">Goods name (English)</p>
                                    </div>
                                    <input  v-model="GoodInformation.name_en" @keyup="nv1 =''" class="customInput" type="text">
                                    <h5 id="" style="margin-left: 30px !important;white-space: nowrap;width:5%" class="m-0 inputTextValue">{{this.GoodInformation.name_en}}</h5>
                                </div>
                                <p class="m-0" style="color:red">{{nv1}}</p>
                                <div  data-status="unOpen" class="textInputDiv textInputDivBorder mt-3"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-tags inputIcon"></i>
                                        <p @click.self="customInputText($event)" style="width:auto;white-space: nowrap;" class="m-0">Goods name (Arabic)</p>
                                    </div>
                                    <input  v-model="GoodInformation.name_ar"  @keyup="nv2 =''" class="customInput" type="text">
                                    <h5 id="" style="margin-left: 30px !important;white-space: nowrap;width:5%" class="m-0 inputTextValue">{{this.GoodInformation.name_ar}}</h5>
                                </div>
                                <p class="m-0" style="color:red">{{nv2}}</p>
                                <div  data-status="unOpen" class="textInputDiv textInputDivBorder  mt-3"  @click.self="customInput($event)" >
                                    <div class="d-flex align-items-center" style="width:40%">
                                        <i class="fa-solid fa-barcode inputIcon"></i>
                                        <p @click.self="customInputText($event)" style="width:auto;white-space: nowrap;" class="m-0">Goods Code</p>
                                    </div>
                                    <input  v-model="GoodInformation.product_sku"  @keyup="skuV =''" class="customInput" type="text">
                                    <h5 id="" style="margin-left: 30px !important;white-space: nowrap;width:5%" class="m-0 inputTextValue ">{{this.GoodInformation.product_sku}}</h5>
                                </div>
                                <p class="m-0" style="color:red">{{skuV}}</p>
                                </div>
                                <div style="background: #FFFFFF;box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);border-radius: 15px 15px 0px 0px;height:104px;display: flex;justify-content: center;align-items: center;">
                                    <button type="submit" class="btn prymaryBtn w-75" style="color:white;height:50px;width:350px !important" >{{requestType}} Goods</button>
                                </div>   
                        </div>
                    </div>
            </div>
            </form>        
            
            <button hidden id="GoodResponsModelButtom" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#GoodResponsModel">
            </button>
            <div class="modal fade" id="GoodResponsModel" tabindex="-1" aria-labelledby="GoodResponsModelLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-body d-flex flex-column">
                            <img src="../assets/bigCheck.png" alt="">
                            <h3 id="GoodResponsText" style="margin-top:16px"></h3>
                            <button class="btn prymaryBtn w-75 " style="color:white;height:50px;margin-top:25px" data-bs-dismiss="modal" aria-label="Close" >Done</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <button hidden id="erorrgoodbtn" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#erorr_good">
              </button>
              
              <div class="modal fade" id="erorr_good" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                  <div class="modal-content">
                    <div class="modal-body d-flex flex-column">
                        <i style="color:red;font-size:xx-large" class="fa-solid fa-circle-exclamation"></i>
                        <h3 style="margin-top:16px">ERROR</h3>
                            <h5>can not delete this good</h5>
                        <button class="btn prymaryBtn w-75" style="color:white;height:50px;margin-top:25px" data-bs-dismiss="modal" aria-label="Close" >Done</button>
                    </div>
                  </div>
                </div>
              </div>

    </div>
</template>

<script>
import Header from '@/components/Custom_Input.vue'
/* eslint-disable */  import functions from  "../assets/js/js.js";
import { mapActions,mapGetters  } from 'vuex';
export default {
name: 'goodsDetails',
components: {
    Header,
}, 
    data(){
        return{
            GoodInformation:{
                name_en:'',
                name_ar:'',
                product_sku:'',
            },
            requestType:'',
            id:'',
            nv1:'',
            nv2:'',
            skuV:'',
        }
    },
    methods:{
    async DeleteGood(id){
        this.$store.dispatch('DeleteGood',id)
    },
    customInput(el){
                functions.customInputa(el)
            },
        customInputText(el){
        el.target.parentElement.parentElement.click()
        },
    Sucond_Btn(){
        const Button = document.getElementById('Sucond_Btn')
        Button.style.display='none';
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='none';
    },
    First_Btn(){
        const First_Btn = document.getElementById('First_Btn')
        First_Btn.style.display='flex';
        First_Btn.querySelector('p').innerText='new Goods'
        First_Btn.addEventListener('click',function() {document.getElementById('addGoodsModalBtn').click();})
    },
    validation(){
        var v = true
        if(this.GoodInformation.name_en.length <= 0){
            this.nv1 = 'This Input Is Required'
            v = false
        }
        if(this.GoodInformation.name_ar.length <= 0){
            this.nv2 = 'This Input Is Required'
            v = false
        }
        if(this.GoodInformation.product_sku.length <= 0){
            this.skuV = 'This Input Is Required'
            v = false
        }
        return v
    },
    AddGood(info){
        if(this.validation()){
            this.$store.dispatch('AddGood', info)
        }
    },
    AddGoodBtn(){
        this.requestType = 'ADD'
        this.GoodInformation.name_en =''
        this.GoodInformation.name_ar = ''
        this.GoodInformation.product_sku =''
    },
    EditGoodBtn(info){
        console.log(info)
        document.getElementById('addGoodsModalBtn').click()
        this.GoodInformation.name_en = info.name_en
        this.GoodInformation.name_ar =  info.name_ar
        this.GoodInformation.product_sku = info.product_sku
        this.id = info.id
        this.requestType = 'EDIT'
    },
    EditGood(info){
        if(this.validation()){
            this.$store.dispatch('EditGoodAction', info)
        }
    },
    },
    computed:{
        ...mapGetters(['Goods'])
    },
    created(){
    document.getElementById('navaya').style.display = 'block'
    document.getElementById('h1h').style.display = 'flex'
    document.getElementById('h2h').style.display = 'none'
        this.Sucond_Btn()
        this.First_Btn()
    },
    mounted() {
    if ( !window.localStorage.token){
    this.$router.push({name:'Login'})
    }
    const Goods = this.$store.dispatch('GetGood')
    console.log(Goods)
  }
}
</script>

<style scoped>

</style>

<style>
.textInputDivBorder{
    border: solid 1px #6F95B5;
}
.tebel-info{
width: 76.5%;
margin: 0;
border-left: 4px dashed #B0B0B0;
padding: 51px;
min-height: calc(100vh - 117px);
}
.goodsDetails{
    width: 100% !important;
    
}
.GinfoDiv{
    border-radius: 15px;
    height: 68px;
    margin-top: 4px !important;
    background-color:#ffffff
}
.GinfoDiv div p{
    font-family: 'Poppins';
    font-style: normal;
    font-size: 14px;
    font-weight: bold;
    line-height: 21px;
    color: black;

}
@media only screen and (max-width: 992px){
        .goodsDetails{
            width: 800px !important; 
            border: none !important;
        }
        .tebel-info{
            overflow-x: auto;
            width: 100%;
            border: none !important;
        }
}
</style>