                    </div>
                </div>
            
            </div>
            <div class="b" style="height:200px;display:none;">
            </div>
            <button id="selected" hidden @click="selectedItems()"></button>
        </div>
        <button hidden id="FinanceModelBtn" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#FinanceModel">
        </button>
        
        <div class="modal fade" id="FinanceModel" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered" style="max-width:75% !important;">
            <div class="modal-content" >
              <div class="modal-body d-flex flex-column" style="background: #F1F1F1 !important;padding: 49px !important;">
                  <h3 style="margin-top:16px" class="mb-4">Calculate  Finance</h3>
                    <div class="orderDiv mt-1" v-for="(order,i) in ListSelectedItems"  v-bind:key="i">
                        <div class="orderDivS1 " style="background-color:white">
                            <div class="d-flex align-items-center h-100">
                                <p class=" orderText x">Ref: 0902934234</p>
                            </div>
                            <div class="d-flex align-items-center">

                            </div>
                        </div>
                        <div class="orderDivS2 row">
                            <div class="info  col-3 m-0 align-items-start">
                                <div class="d-flex align-items-center">
                                    <i class="fa-solid fa-tags "></i>
                                    <p class="m-0">Brand</p>
                                </div>
                                <h6 class="text-center m-0">{{order.brand_name}}</h6>
                            </div>
                            <div class="info  col-3 m-0 align-items-start">
                                <div class="d-flex align-items-center">
                                    <i class="fa-solid fa-box "></i>
                                    <p class="m-0">Goods Name</p>
                                </div>
                                <h6 class="text-center m-0">{{order.goods_name}}</h6>
                            </div>
                            <div class="info  col-3 m-0 align-items-start">
                                <div class="d-flex align-items-center">
                                    <i class="fa-solid fa-box"></i>
                                    <p class="m-0">Container</p>
                                </div>
                                <h6 class="text-center m-0">{{order.cartons}}</h6>
                            </div>
