                                    <i class="fa-solid fa-clipboard"></i>
                                    <p class="m-0">Pay Amount</p>
                                </div>
                                <h6 class="text-center m-0" style="color:#6AAA69"> {{order.payment}} K.D</h6>
                            </div>
                        </div>
                    </div>
                    <div class="orderDivTotal row mt-1 ">
                            <div class="  col-9 m-0 align-items-center justify-content-start d-flex gap-4">
                                <h3 class="m-0" style="color:white;width:auto !important">Total AMount</h3>
                                <h5 class="m-0" style="color:#B0B0B0">( {{ itemsIsSelected }} ITEMS )</h5>
                            </div>
                            <div class="d-flex col-3 m-0 align-items-center justify-content-start">
                                <h3 class="text-center m-0" style="color:white">{{total}} K.D</h3>
                            </div>

                    </div>

                  <button v-if="itemsIsSelected > 0" class="btn prymaryBtn w-75" style="color:white;height:50px;margin-top:25px" @click="culc" >Done</button>
                  <button v-else class="btn prymaryBtn w-75" style="color:white;height:50px;margin-top:25px" data-bs-dismiss="modal" aria-label="Close" >close</button>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import _axios from '@/axios';
import { mapGetters  } from 'vuex';
import Header from '@/components/Custom_Input.vue'
/* eslint-disable */  import functions from  "../assets/js/js.js";
import router from '../router';
export default {
    name:'FinancePage',
    components: {
    Header,
    }, 
    data(){
        return{
            itemsIsSelected:0,
            ListSelectedItems:[],
            total:0,
            caulcOrders:[],
        }
    },
    methods:{
    Sucond_Btn(){
        const Button = document.getElementById('Sucond_Btn')
        Button.style.display='none';
        const historyBtn = document.getElementById('historyBtn')
        historyBtn.style.display='flex';
    },
    First_Btn(){
        const First_Btn = document.getElementById('First_Btn')
        First_Btn.style.display='flex';
        First_Btn.querySelector('p').innerText='Start Count'
        First_Btn.querySelector('p').style.width='100%'
        First_Btn.querySelector('img').style.display='none'
        First_Btn.addEventListener('click',function(){document.getElementById('selected').click()})
    },
    select(item){
        item.selected = !item.selected
        if(item.selected){
            item.selected = !item.selected
            var items =this.$store.state.OrderContaner.InfoListSelected
            for(var i=0;i<items.length;i++){
                if(items[i].id === item.id){
                    this.$store.state.OrderContaner.InfoListSelected[i].selected=true
                }
            this.itemsIsSelected = this.$store.state.OrderContaner.InfoListSelected.filter(function(item) {
            return item.selected;
            }).length;
            }
            const First_Btn = document.getElementById('First_Btn')
            First_Btn.querySelector('p').innerText=`Calculate (${this.itemsIsSelected} Items)`
        }else{
            item.selected = !item.selected
            var items =this.$store.state.OrderContaner.InfoListSelected
            for(var i=0;i<items.length;i++){
                if(items[i].id === item.id){
                    this.$store.state.OrderContaner.InfoListSelected[i].selected=false
                }
            this.itemsIsSelected = this.$store.state.OrderContaner.InfoListSelected.filter(function(item) {
            return item.selected;
            }).length;
            }
            const First_Btn = document.getElementById('First_Btn')
            First_Btn.querySelector('p').innerText=`Calculate (${this.itemsIsSelected} Items)`
        }
        this.ListSelectedItems = this.$store.state.OrderContaner.InfoListSelected.filter(function(item){
            return item.selected;
            })
            this.caulcOrders = []
            this.total = 0
            for(var t = 0; t < this.ListSelectedItems.length; t++){
                this.caulcOrders.push(this.ListSelectedItems[t].id)
                this.total += this.ListSelectedItems[t].payment
            }

    },
    selectedItems(){
        var selectedItems = document.getElementsByClassName('selectedItems')
        for (let i = 0; i < selectedItems.length; i++) {
            // selectedItems[i].style.visibility = "visible";
            selectedItems[i].style.display = "block";
        }
        const First_Btn = document.getElementById('First_Btn')
        First_Btn.querySelector('p').innerText=`Calculate (${this.itemsIsSelected} Items)`
        First_Btn.addEventListener('click',function(){document.getElementById('FinanceModelBtn').click()})
    },
    culc(){
        console.log(this.caulcOrders)
        _axios.post('/api/histories',{
        "containers":this.caulcOrders}, { headers: {
                'Authorization': `token ${window.localStorage.token}`}
              })
            .then(response => {
                response
                document.getElementById("FinanceModelBtn").click()
                this.$router.push({name:'History'})
                // contex.dispatch('Gethistories')
            })
            .catch(error => {
                console.log('Error')
                console.error(error);
            });
    }
    },
    computed:{
        ...mapGetters(['OrdersSelect']),

    },
    created(){
        document.getElementById('navaya').style.display = 'block'
        document.getElementById('h1h').style.display = 'flex'
        document.getElementById('h2h').style.display = 'none'
        this.Sucond_Btn()
        this.First_Btn()
        this.$store.dispatch('GetOrders')
    },
    mounted(){
        if ( !window.localStorage.token){
      this.$router.push({name:'Login'})
    }
    }

} 
</script>

<style>
.empty_check{
    background-color: #D9D9D9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
</style>