<template>

    <div class="loginPage py-5" style="">
        <div class="d-flex flex-column align-items-center ">
            <img class="logo mb-4" alt="logo" style="width:230px" src="../assets/logo.png">  
            <div class="d-flex flex-column gap-2 mb-2">
                <h2 class="text-center">Sign IN</h2>
                <p class="text-center">Enter yout Email</p>
            </div>
            <form  class="d-flex flex-column align-items-center">
                <div class="d-flex flex-column " style="gap:10px">
                    <div  data-status="unOpen" class="textInputDiv mb-3"  @click.self="customInput($event)" >
                        <div class="d-flex align-items-center" style="width:40%">
                            <i class="fa-solid fa-at inputIcon"></i>
                            <p @click.self="customInputText($event)" class="m-0">EMAIL</p>
                        </div>
                        <input name="email" v-model="UserInfo.email" class="customInput" type="email">
                        <h5 id="" style="margin-left: 30px !important;" class="m-0 inputTextValue">{{this.UserInfo.email}}</h5>
                    </div>
                    <div  data-status="unOpen" class="textInputDiv"  @click.self="customInput($event)" >
                        <div class="d-flex align-items-center" style="width:40%">
                            <i class="fa-solid fa-unlock inputIcon"></i>
                            <p @click.self="customInputText($event)" class="m-0">PASSWORD</p>
                        </div>
                        <input name="password" v-model="UserInfo.password" class="customInput" type="password">
                        <h5 id="" style="margin-left: 30px !important;" class="m-0 inputTextValue ">{{this.setStar}}</h5>
                    </div>
                </div>
                <a @click="login(UserInfo)"  class="btn prymaryBtn w-75 mt-4" style="color: white; height: 50px; width: 390px !important;">LOGIN</a>             
            </form>
        </div>
        <div class="b"></div>

        <button hidden id="loginerrorbtn" type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#loginerror">
              </button>
              
              <div class="modal fade" id="loginerror" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                  <div class="modal-content">
                    <div class="modal-body d-flex flex-column">
                        <i style="color:red;font-size:xx-large" class="fa-solid fa-circle-exclamation"></i>
                        <h3 style="margin-top:16px">ERROR</h3>
                            <h5>Email Or Password Invalid</h5>
                        <button class="btn prymaryBtn w-75" style="color:white;height:50px;margin-top:25px" data-bs-dismiss="modal" aria-label="Close" >Done</button>
                    </div>
                  </div>
                </div>
              </div>

    </div>

</template>

<script>


/* eslint-disable */  import functions from  "../assets/js/js.js";
export default {
name: 'Login',
components: {
},
data(){
    return {
        UserInfo:{
            email:'',
            password:'',
        }
    }
},
methods:{
    customInput(el){
                functions.customInputa(el)
            },
    customInputText(el){
        el.target.parentElement.parentElement.click()
        },
    login(i){
          this.$store.dispatch('login',i)
        },
},
computed:{
    setStar(){
        function wordWrapToStringList (text, maxLength) {
            var textList=[]
            for(let  c = 1 ; c <= maxLength ;c++){
                textList.push(text)
            }
        console.log(textList)
        return textList.join('')
        };
        return wordWrapToStringList('*',this.UserInfo.password.length)
    }
},
created(){
    document.getElementById('h1h').style.display = 'none'
    document.getElementById('h2h').style.display = 'none'
    document.getElementById('navaya').style.display = 'none'
}
}
</script>

<style>


.loginPage{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
gap: 19px;
width: 100%;
margin: 0;

min-height: calc(100vh - 117px) !important;
}
@media only screen and (max-width: 992px){
    .loginPage{
        width: 100%;
        }
}
</style>